.switchButton {
  width: 75px;
  height: 30px;
  display: inline-block;
  background-color: #e63946;
  border-radius: 20px;
  position: absolute;
  left: 80%;
  top: 10px;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
    pointer-events: none;
  }

  &__checkbox {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 2;

    &:checked + .switchButton__checkbox__label:before {
      opacity: 0;
      // transition: all 0.8s ease-out;
    }

    & + .switchButton__checkbox__label {
      user-select: none;
      pointer-events: none;

      &:before {
        //boton posandose arriba
        content: "";
        background: #1d3557;
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 30px;
      }
    }
  }
}

main {
  line-height: 100px;
  transition: all 0.5s;
  -webkit-text-stroke: 4px #1d3557;
  font-variation-settings: "wght" 900, "ital" 1;
  font-size: 7rem;
  text-align: center;
  color: transparent;
  font-family: "Meta", sans-serif;
  text-shadow: 10px 10px 0px #00f7ff, 15px 15px 0px #e601c0,
    20px 20px 0px #e9019a, 25px 25px 0px #f40468, 45px 45px 10px #482896;
  cursor: pointer;
}

main:hover {
  font-variation-settings: "wght" 100, "ital" 0;
  text-shadow: none;
}

.navBar {
  &__menu {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 10%;
    left: 44%;
    transform: translateX(-50%);
    padding-left: 10px;
    & svg path {
      fill: #1d3557;
    }
    & a {
      font-size: 10px;
    }

    @media (min-width: 768px) {
      display: flex;
      flex-direction: column;
      position: fixed;
      top: 20%;
      transform: translateY(-50%);
      padding-left: 10px;
      left: 0;
    }

    &__button {
      width: 50px;
      height: 50px;
      margin-bottom: 10px;
      display: inline-block;
      background-color: #b9a4ee;
      color: #f0f3bd;
      text-align: center;
      text-decoration: none;
      font-size: 40px;
      border-radius: 50px;
      margin-left: 40px;
    }
    &__button:hover {
      background-color: #b9a4ee;
      width: 200px;
      transition: 0.7s;
    }
    #navBar-home,
    #navBar-contact,
    #navBar-document {
      font-family: "Raleway", sans-serif;
      font-size: 15px;
      color: #482896;
      font-weight: 600;
      line-height: 40px;
      & svg {
        color: #482896;
      }
    }
  }
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  left: 50%;
  top: 50%;
  position: absolute;
  transform: translate(-50%, -50%);

  &__list {
    &__menu {
      display: inline-flex;
      list-style: none;
      margin: 0px;
      & li {
        margin: 10px;
        min-width: 60px;
        text-align: center;
        & svg path {
          fill: #1d3557;
        }
      }
    }
  }

  .title {
    & h1 {
      text-align: center;
      margin-top: 100px;
      font-family: "Raleway", sans-serif;
      font-size: 35px;
      color: #1d3557;
      font-weight: 600;
      line-height: 40px;
    }
  }

  .title2 {
    width: 300px;
    align-self: flex-end;
    margin-left: 25px;
    & p {
      text-align: center;
      font-style: italic;
      margin-top: 25px;
      margin-bottom: 25px;
      font-family: "Raleway", sans-serif;
      font-size: 20px;
      color: #e601c0;
      font-weight: 500;
    }
    @media (min-width: 1024px) {
      align-self: center;
    }
  }
}

