.loginBox {
  margin-top: 20px;
  margin-right: 100px;
  position: absolute;
  // top: 50%;
  // left: 50%;
  right: -40px;
  width: 300px;
  padding: 40px;
  //transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}

@media (min-width: 768px) {
  .loginBox {
    margin-top: 70px;
    right: 0;
    transform: translate(-50%);
  }
}

@media (min-width: 1024px) {
  .loginBox {
    margin-top: 70px;
    right: 0;
    transform: translate(-50%);
    left: 50%;
  }
}

.loginBox h2 {
  margin: 0 0 30px;
  padding: 0;
  color: #b9a4ee;
  text-align: center;
}

.loginBox {
  &__user {
    position: relative;
  }
}

.loginBox {
  &__user {
    & input {
      width: 100%;
      padding: 10px 0;
      font-size: 16px;
      color: #b9a4ee;
      margin-bottom: 30px;
      border: none;
      border-bottom: 1px solid #fff;
      outline: none;
      background: transparent;
    }
  }
}

.loginBox {
  &__user {
    & label {
      position: absolute;
      top: 0;
      left: 0;
      padding: 10px 0;
      font-size: 16px;
      color: #b9a4ee;
      pointer-events: none;
      transition: 0.5s;
    }
  }
}

.loginBox form a {
  position: relative;
  display: inline-block;
  margin-left: 42px;
  padding: 10px 20px;
  color: #b9a4ee;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 40px;
  letter-spacing: 4px;
}

.loginBox a:hover {
  background: #1d3557;
  color: #fff;
  border-radius: 5px;
}

.loginBox a span {
  position: absolute;
  display: block;
}

.loginBox a span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #e601c0);
  animation: btn-anim1 1s linear infinite;
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.loginBox a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #e601c0);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.loginBox a span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #e601c0);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.loginBox a span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #e601c0);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
